@media (min-width: 992px) {
  .custom-dropdown.language-dropdown {
    margin-left: auto;
  }

  .custom-dropdown.language-dropdown:hover .dropdown-menu {
    display: block !important;
  }

  .custom-dropdown.language-dropdown:hover .btn:after {
    transform: rotate(225deg);
  }
}

.custom-dropdown.language-dropdown .dropdown-menu {
  border: none !important;
  border-radius: 0.25rem !important;
  box-shadow: 0 4px 16px -4px #051f4e1a, 0 10px 32px -8px #051f4e29;
  display: none !important;
  margin-top: -3px;
}

@media (max-width: 991px) {
  .custom-dropdown.language-dropdown .dropdown-menu {
    border: none;
    box-shadow: none;
    left: 5px !important;
    width: 100%;
  }
}

.custom-dropdown.language-dropdown .dropdown-menu.show {
  display: block !important;
}

.custom-dropdown.language-dropdown .dropdown-menu:after {
  background: #fff;
  content: "";
  height: 16px;
  left: 50%;
  position: absolute;
  top: 0;
  transform: rotate(45deg) translate(-60%);
  width: 16px;
}

.custom-dropdown.language-dropdown .dropdown-menu a {
  color: #051f4e;
  font-size: 14px !important;
  font-weight: 600;
  padding-left: 23px;
}

.custom-dropdown.language-dropdown .dropdown-menu a:active,
.custom-dropdown.language-dropdown .dropdown-menu a:hover {
  color: #051f4e !important;
}

.custom-dropdown.language-dropdown .dropdown-menu a:focus {
  color: #fff !important;
}

@media (min-width: 992px) {
  .custom-dropdown.language-dropdown .dropdown-menu a {
    padding-left: 30px;
  }
}

@media (max-width: 991px) {
  .custom-dropdown.language-dropdown.show .header-language-dropdown:after {
    content: "\f106";
  }
}

.custom-dropdown img {
  border: 2px solid #fff;
  border-radius: 50%;
  display: inline-block;
  margin-right: 10px;
  position: relative;
  top: -1px;
  vertical-align: middle;
  width: 20px;
}

.custom-dropdown .btn {
  background-color: initial !important;
  color: #fff;
  font-size: 14px;
  font-weight: 600;
  margin-bottom: 0;
  padding-right: 40px;
}

@media (min-width: 992px) {
  .custom-dropdown .btn:after {
    border: none;
    border-bottom: 1px solid #051f4e !important;
    border-right: 1px solid #051f4e !important;
    content: "\f107";
    font-family: Font Awesome\5 Pro;
    font-size: 20px;
    font-weight: 400;
    position: absolute;
    right: 17px;
    top: 52%;
    transform: translateY(-50%);
  }
}

.custom-dropdown .btn:focus {
  box-shadow: none !important;
}

.custom-dropdown .btn.header-language-dropdown {
  border: none !important;
  border-radius: 0 !important;
  color: #051f4e !important;
  font-size: 16px !important;
  padding-right: 40px;
  position: relative;
}

@media (max-width: 991px) {
  .custom-dropdown .btn.header-language-dropdown {
    padding-left: 20px;
    text-align: left;
    width: 100%;
  }

  .custom-dropdown .btn.header-language-dropdown:after {
    border-top: 0;
    color: #051f4e;
    content: "\f107";
    display: block;
    font-family: Font Awesome\5 Pro;
    font-size: 20px;
    font-weight: 400;
    line-height: 1;
    position: absolute;
    right: 25px;
    top: 50%;
    transform: translateY(-50%);
  }
}

@media (min-width: 992px) {
  .custom-dropdown .btn.header-language-dropdown {
    padding-bottom: 20px;
    padding-top: 20px;
  }
}

.custom-dropdown .btn.header-language-dropdown .language-name {
  color: #051f4e;
  margin-left: 0 !important;
}

.custom-dropdown .btn.header-language-dropdown:before {
  content: "\f0ac";
  font-family: Font Awesome\5 Pro;
  font-size: 20px;
  font-weight: 400;
  margin-right: 10px;
}

.custom-dropdown .dropdown-item {
  padding-bottom: 10px !important;
  padding-top: 10px !important;
}

.custom-dropdown:hover .dropdown-menu {
  display: block;
}

.youtube-holder .inner-holder {
  display: inline-block;
  position: relative;
}

.youtube-holder .fa-youtube {
  font-size: 50px;
  left: 50%;
  position: absolute;
  top: 50%;
  transform: translate(-50%, -50%);
}

:root {
  --height-overlay: calc(100% - 70px);
}

@media (min-width: 1600px) {
  .d-xxl-block {
    display: block !important;
  }

  .d-xxl-none {
    display: none !important;
  }
}

.dropdown-menu .wrapper.has-children {
  position: relative;
}

.dropdown-menu .wrapper.has-children .dropdown-menu {
  display: none;
  left: 100%;
  position: absolute;
  top: 0;
}

.dropdown-menu .wrapper.has-children:after {
  content: "\f105";
  font-family: Font Awesome\5 Pro;
  position: absolute;
  right: 10px;
  top: 50%;
  transform: translateY(-50%);
}

.dropdown-menu .wrapper.has-children:hover .dropdown-menu {
  display: block;
}

.register-submit-button {
  margin-top: 30px;
}
